.pagetitle{
    font-size: 1.875rem;
    font-weight: bold;
    display: flex;
    align-content: center;
  }
  a {
    text-decoration: none;
    color: #ffffff;
   }
   li {
    list-style: none;
   }
  .navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    background-color: #5860a8;
    color: #ffffff;
   }
   .nav-links a {
    color: #ffffff;
   }
  
   /*.logo {
    font-size: 1.3rem;
  
   }
   .logo:hover{
    background-color: #d0d4ff;
    border-radius: 5px;
    transition: 300ms;
   }*/
   
   .menu {
    display: flex;
    gap: 1em;
    font-size: 1.3rem;
   }
   .menu li:hover {
    background-color: #d0d4ff;
    border-radius: 5px;
    transition: 300ms;
   }
   .menu li {
    padding: 5px 14px;
   }
  
  
  input[type=checkbox]{
    display: none;
   } 
  
   .hamburger {
    display: none;
    font-size: 24px;
    user-select: none;
   }
  
   @media (max-width: 940px) {
    .pagetitle{
      font-size: 1.3rem;
      font-weight: bold;
      display: flex;
      align-content: center;
    }
   .menu { 
    margin-top: 0.8rem;
    display:none;
    position: absolute;
    background-color:#5860a8;
    right: 0;
    left: 0;
    text-align: center;
    padding: 16px 0;
   }
   .menu li:hover {
    display: inline-block;
    background-color:#d0d4ff;
    transition: 300ms ease;
   }
   .menu li + li {
    margin-top: 0.8rem;
   }
   input[type=checkbox]:checked ~ .menu{
    display: block;
   }
   .hamburger {
    display: block;
   }
   
  }